$size-clock: 500px;
$bgcolor-stopwatch: #fff;
$borded-color-stopwatch: #e0d2d2;

$second-hand-width: 10px;
$second-hand-height: 250px;
$second-hand-color: #ff0040;
$second-duration: 60s;

$minute-hand-width: 18px;
$minute-hand-height: 125px;
$minute-hand-color: #e0d2d2;
$minute-duration: 60 * $second-duration;

$dot-size: 15px;
$dot-bgcolor: #392b44;
